import {mapState} from 'vuex'
export default {
	data() {
		return {
			
		};
	},
	computed: {
		...mapState([
			"userid"
		])
	},
	onLoad(){
	},
	onReady(){
		
		uni.showToast({
			title: this.userid,
			duration: 2000,
			icon: 'none'
		});
		if(this.userid == null){
			uni.redirectTo({
				url: '/pages/login/phoneLogin'
			});
		}
	}
}
